* {
    margin: 0;
    padding: 0;
    font-family: Roboto;
    background-color: var(--background-color);
    transition: background-color 300ms linear;
}

body::-webkit-scrollbar {
    width: 10px;
}
  
body::-webkit-scrollbar-track {
    background-color: var(--background-el);
    border-radius: 0 0 8px 0 ;
}

body::-webkit-scrollbar-thumb {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background: #7B7B7B;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.12);
    border-radius: 10px;
    transform: matrix(1, 0, 0, -1, 0, 0);
}

.container {
    margin-right: auto;
    margin-left: auto;
    width: 100%;
}

.col{
    justify-self: center;
}

@media (min-width: 1366px) {
    .container{
        max-width: 1120px;
    }
}

@media (min-width: 1024px) and (max-width: 1365px) {
    .container {
        max-width: 940px;
    }
    
}

@media (min-width: 768px) and (max-width: 1023px) {
    .container{
        max-width: 700px;
    }
    .featured-news {
        grid-template-columns: 1fr  1fr ;
    }
}

@media (min-width: 320px) and (max-width: 767px){
    .container {
        max-width: 90%;
    }
    .featured-news {
        grid-template-columns: 1fr ;
    }
}

html[data-theme='dark']{
    --background-color: #000;
    --color: #fff;
    --background-el: #0C0C0C;
    --border:  1px solid #FFFFFF;
    --background-pages:  #464646;
    --color-input: rgba(255, 255, 255, 0.3);
    --input-back: #fff; 
    --border-filter: rgba(255, 255, 255, 0.3);
}

html[data-theme='light']{
    --background-color: #fff;
    --color: #000;
    --background-el: rgba(255,255,255, 1);
    --border: 1px solid #000;
    --background-pages:  #EDEDED;
    --color-input: rgba(0, 0, 0, 0.3);
    --input-back: #EFEFEF; 
    --border-filter: rgba(0, 0, 0, 0.3);
}

